body {
  font-family: 'Roboto', sans-serif;
  text-align: center;
  background-color: antiquewhite;
}

img {
  width: 20vw;
  margin-bottom: 5px;
}

h1 {
  background-color: black;
  color: antiquewhite;
  font-size: 3rem;
}

h2 {
  font-family: 'Merriweather', serif;
  margin-bottom: 0;
  font-size: 2rem;
  color: rgb(83, 92, 223);
}

h3 {
  font-family: 'Merriweather', serif;
  background-color: black;
  color: antiquewhite;
}

h4 {
  font-family: 'Merriweather', serif;
  margin-bottom: 0.5rem;
}

div {
  margin: 10px 0;
}

p {
  font-size: 1rem;
  padding: 5px 20px;
}

.pBio {
  padding: 5px 10vw;
  margin-top: 0;
}

.pYear {
  margin-top: 0;
  font-style: italic;
  color: rgb(83, 92, 223);
}

.pPlot {
  padding: 5px 10vw;
  margin-top: 0;
}

.movie-view-container {
  position: relative;
}

.scroll-button {
  background-color: rgba(250, 235, 215, 0.9);
  color: rgb(83, 92, 223);
  padding: 10px 20px;
  border: 2px solid rgb(83, 92, 223);
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s, border 0.3s;
}

.scroll-button:hover {
  background-color: rgb(83, 92, 223);
  color: antiquewhite;
  border: 2px solid rgb(83, 92, 223);
}

.button-container {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin: 20px 0;
}

.movie-title {
  cursor: pointer;
  transition: font-size 0.3s ease-in-out;
}

.movie-card:hover .movie-title {
  color: rgb(83, 92, 223);
  font-size: 1.2em;
}
