/* AuthViewDark.scss */

.auth-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: black; /* Schwarzer Hintergrund */
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); /* Dunkler Schatten */
  margin: 20px auto;
  max-width: 400px;
}

.auth-heading {
  font-size: 1.5em;
  color: rgba(83, 92, 223, 0.9); /* Farbe für den Heading */
  margin-bottom: 20px;
}

.auth-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.auth-label {
  margin: 10px 0;
  font-family: 'Roboto', sans-serif;
  width: 100%;
  text-align: left;
  color: white; /* Farbe für das Label */
}

.auth-input {
  padding: 10px;
  margin: 5px 0;
  border: none; /* Kein Rand */
  border-radius: 5px;
  width: calc(100% - 22px); /* Platz auf der rechten Seite schaffen */
  background-color: antiquewhite; /* Antiquewhite Hintergrundfarbe */
  color: black; /* Schwarzer Text */
}

.auth-button {
  padding: 12px 20px; /* Größere Padding beim Submit-Button */
  background-color: rgba(
    83,
    92,
    223,
    0.9
  ); /* Hintergrundfarbe mit Transparenz */
  color: fff; /* Weiße Textfarbe */
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, padding 0.3s; /* Übergangseffekt für Hintergrundfarbe und Padding */
  margin-top: 20px;
}

.auth-button:hover {
  background-color: rgba(83, 92, 223, 1); /* Aufhellung beim Hover */
  color: #fff;
  padding: 14px 22px; /* Größere Padding beim Hover */
}
